					<div class="yourong-direction-intro">山东有容律师事务所致力于为客户提供全方位、高效、专业的法律服务。</div>
					<div class="yourong-direction-text">
						业务范围涵盖房地产与建设工程、银行与金融、行政诉讼、劳动与雇佣、民商事诉讼与仲裁、家事业务与私人财富管理、知识产权、刑事、环境与资 源、海事海商、公司综合类业务等专业领域。</div>
					<div class="yourong-direction-text" style="margin-top: 0.4rem;">
						更在应收帐款、法律顾问、刑事、民商事等领域拥有标准化、专业化、精细化、品质化的法律服务产品。</div>
				</div>
				<div class="yourong-direction-content" v-else>
					<div class="yourong-direction-title">专业人才</div>
