					<div class="yourong-direction-text">
						所内现有执业律师、实习律师、律师助理以及其他专业工作人员共计数十人，办案律师均深耕律师业务多年，是一支拥有丰富实践经验和深厚理论造诣高效团结、年富力强的中青年专业律师队伍。</div>
					<div class="yourong-direction-text" style="margin-top: 0.4rem;">
