<script>
	import {
		getContactUsList,
		getProfessionalFieldList
	} from '../../apis/openapi.js';
	import UTCUtil from '../../../static/js/UTCUtil.js';
	import {
		swiper,
		swiperSlide
	} from 'vue-awesome-swiper';
	import 'vue-awesome-swiper/node_modules/swiper/dist/css/swiper.css'; //这里注意具体看使用的版本是否需要引入样式，以及具体位置。
	export default {
		metaInfo:window.$VueMetaInfo['home'],
		components: {
			swiper,
			swiperSlide,
		},
		data() {
			return {
				bannerList: [{
						bannerUrl: require("../../../static/image/home-app/banner-one.png"),
					},
					{
						bannerUrl: require("../../../static/image/home-app/banner-two.png"),
					},
					{
						bannerUrl: require("../../../static/image/home-app/banner-three.png"),
					},
				],
				swiperOptions: {
					loop: true,
					speed: 2000,
					autoplay: {
						delay: 3000,

						stopOnLastSlide: false,
						disableOnInteraction: false,
					},
					// 显示分页
					pagination: {
						el: ".swiper-pagination",
						clickable: true, //允许分页点击跳转
					},
					// 设置点击箭头
					navigation: {
						nextEl: ".swiper-button-next",
						prevEl: ".swiper-button-prev",
					},
				},
				enterpriseInfo: {}, //企业信息
				fieldList: [], //专业领域
				isDirection: true,
				styleSwitch:true,
			}
		},
		computed: {
			swiper() {
				return this.$refs.bannerSwiper.swiper
			}
		},
		mounted() {
			this.getProfessionalFieldList()
			if (this.$store.state.companyInfo) {
				this.enterpriseInfo = this.$store.state.companyInfo
			} else {
				this.getContactUsList()
			}

		},
		methods: {
			//点击查看更多
			changeDirection(){
				this.isDirection = !this.isDirection
				this.styleSwitch = false
				setTimeout(()=>{
					this.styleSwitch = true
				},1000)
			},
			//查询专业领域信息
			getProfessionalFieldList() {
				let params = {
					page: 1,
					rows: 12
				}
				getProfessionalFieldList(params).then(res => {
					if (res.data.success == 'true') {
						this.fieldList = res.data.data.data
					}
				});
			},
			//查询企业信息
			getContactUsList() {
				getContactUsList({}).then(res => {
					if (res.data.success == 'true') {
						this.enterpriseInfo = res.data.data
					}
				});
			},
			//跳转分类（路由）
			toggleNavigation(url, fieldIndex, uuid) {
				this.$router.push({
					path: url,
					query: {
						fieldIndex: fieldIndex,
						fieldUuid: uuid
					}
				});
				//this.$forceUpdate()
			},
			//跳转到联系我们
			toContactUs(id) {
				this.$router.push({
					path: 'contact-us-app',
					query: {
						id: id,
					}
				});
			},

		}
	}
</script>
